*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}
body {
    display: flex;
    flex-direction: column;
    padding: 0 1em;
}

.cart {
    display: flex;
    flex-direction: column;

    .items-details {
        display: flex;
        flex-direction: column;
        gap: 2em;
        padding: 2em;
        background-color: #F2F2F2;
        border-radius: 12px;

        .item {
            display: flex;
            align-items: center;
            gap: 2em;

            #product-image {
                max-width: 8em;
            }

            .product-metadata{
                // outline: red solid 1px;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                gap: 3em;
                // height: 8em;


                .name-price {
                    display: flex;
                    flex-direction: column;
                    gap: 1em;

                    .price {
                        display: flex;
                        justify-content: space-between;
                    }
                }

                .item-count {
                    display: flex;
                    justify-content: center;
                    gap: 1.5em;
                }
            }
        }
    }
}